import "./App.css";
import React, { useState } from "react";
import FadeIn from "react-fade-in";
import data from "./vv-data-new2.json";
import logoTwitter from "./media/tweetr.png";
import logoInsta from "./media/insta.png";

function App() {
  const rand = Math.floor(Math.random() * 362);
  const value = data[rand];

  // console.log(rand);
  // console.log(value);

  function removeLink(words) {
    return words.split(" ").slice(0, -1).join(" ");
  }

  return (
    <div className="app">
      <FadeIn transitionDuration={1400}>
        <img
          className="img"
          src={value["Media URL"]}
          alt="vv"
          // onClick={() => setRand(Math.floor(Math.random() * 421))}
        />
        <p className="title">{removeLink(value["Tweet content"])}</p>
        <div className="row">
          <a href="https://twitter.com/visualizevalue">
            <img className="social" src={logoTwitter} alt="vv" />
          </a>
          <a href="https://www.instagram.com/visualizevalue/">
            <img className="social" src={logoInsta} alt="vv" />
          </a>
        </div>
      </FadeIn>
    </div>
  );
}

export default App;
